const t0 = [
  [0, 0, 0],
  [1, 1, 1],
  [0, 1, 0],
];

const t1 = [
  [0, 1, 0],
  [1, 1, 0],
  [0, 1, 0],
];

const t2 = [
  [0, 1, 0],
  [1, 1, 1],
  [0, 0, 0],
];

const t3 = [
  [0, 1, 0],
  [0, 1, 1],
  [0, 1, 0],
];

const t = [t0, t1, t2, t3];

export default t;




